body {
  background-image: url(bg0.png);
  background-repeat: repeat-y;
}

.icons-notext.dark > span {
  color: white;
}
.icons-notext.dark > span,
.icons-notext > span {
  display: inline-block;
  min-width: 100px;
  position: relative;
  top: -7px;
  left: -8px;
}
.App {
  padding: 20px 30px 20px 60px;
}
h1 {
  margin: 0 0 10px;
}
h3 {
  margin-top: 30px;
}
header {
  padding: 10px 0;
}
.codeTab {
  margin-left: 20px;
}
