  max-width: 50%;
}
*/

a {
  color: #39c;
  font-weight: 600;
  text-decoration: none;
}
a:hover {
  color: navy;
}

i {
  box-sizing: border-box;
}
.icons > div,
.icons-notext > div {
  display: inline-block;
  // margin: 10px;
  width: 190px;
  > span {
    position: relative;
    top: -7px;
    left: -5px;
  }
}
.icons svg,
.icons-notext svg {
  position: relative;
}
.icons .crud-icon {
  top: 6px;
  margin-right: 4px;
}
.icons-notext.dark {
  background-color: black;
  color: white;
}
.icons-notext i {
  display: inline-block;
  margin: 10px;
}

.sizes {
  padding: 20px;
  /* @include flex-holder(); */
}
.sizes.dark {
  background-color: black;
}
.sizes > div {
  display: inline-block;
}
.evo-home {
  background-color: white;
}
.evo-home .version {
  font-size: 0.5em;
}
code {
  display: block;
  margin: 10px 0;
  padding: 20px 40px 20px 10px;
  background-color: #fafafa;
  border: solid 1px silver;
  font-size: 14px;
}
header {
  position: relative;
}
.github {
  position: absolute;
  top: 10px;
  right: 10px;
  margin: 0 0 10px 30px;
  font-size: 20px;
}
.github > svg {
  fill: black;
}
.github:hover > svg {
  fill: #39c;
}
